<template>
  <el-dialog
    v-model="visible"
    title="‰øÆÊîπÂØÜÁ†Å"
    width="500px"
    :close-on-click-modal="false"
    :show-close="false"
    :close-on-press-escape="false"
    destroy-on-close
  >
    <el-alert
      title="È¶ñÊ¨°ÁôªÂΩïÈúÄË¶Å‰øÆÊîπÂØÜÁ†Å"
      type="warning"
      :closable="false"
      show-icon
      style="margin-bottom: 20px"
    />
    
    <el-form
      ref="formRef"
      :model="form"
      :rules="rules"
      label-width="120px"
      @submit.prevent
    >
      <el-form-item label="Êñ∞ÂØÜÁ†Å" prop="new_password">
        <el-input
          v-model="form.new_password"
          type="password"
          placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
          show-password
          clearable
          @keyup.enter="handleSubmit"
        />
      </el-form-item>
      
      <el-form-item label="Á°ÆËÆ§ÂØÜÁ†Å" prop="confirm_password">
        <el-input
          v-model="form.confirm_password"
          type="password"
          placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
          show-password
          clearable
          @keyup.enter="handleSubmit"
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleSubmit" type="primary" :loading="loading">
          Á°ÆËÆ§‰øÆÊîπ
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import request from '@/utils/request'

const emit = defineEmits<{
  (event: 'success'): void
  (event: 'close'): void
}>()

const visible = ref(false)
const loading = ref(false)
const adminId = ref(0)

const form = reactive({
  new_password: '',
  confirm_password: ''
})

const formRef = ref()

const rules = {
  new_password: [
    { required: true, message: 'ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å', trigger: 'blur' },
    { min: 8, message: 'ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë8‰Ωç', trigger: 'blur' },
    { 
      validator: (rule: any, value: string, callback: any) => {
        if (value && value.length < 8) {
          callback(new Error('ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë8‰Ωç'))
        } else if (value && !/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^a-zA-Z\d])/.test(value)) {
          callback(new Error('ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Â§ßÂ∞èÂÜôÂ≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅÁâπÊÆäÂ≠óÁ¨¶'))
        } else {
          callback()
        }
      }, 
      trigger: 'blur' 
    }
  ],
  confirm_password: [
    { required: true, message: 'ËØ∑Á°ÆËÆ§ÂØÜÁ†Å', trigger: 'blur' },
    {
      validator: (rule: any, value: string, callback: any) => {
        if (value !== form.new_password) {
          callback(new Error('‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥'))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

const open = (userId: number) => {
  visible.value = true
  adminId.value = userId
  // Ê∏ÖÁ©∫Ë°®Âçï
  form.new_password = ''
  form.confirm_password = ''
}

const handleSubmit = async () => {
  try {
    await formRef.value?.validate()
    loading.value = true
    
    const response = await request.post('/api/v1/admin/change-password', {
      admin_id: adminId.value,
      new_password: form.new_password,
      confirm_password: form.confirm_password
    })
    
    console.log('ÂØÜÁ†Å‰øÆÊîπAPIÂìçÂ∫î:', response)
    
    if (response.code === 200) {
      ElMessage.success('ÂØÜÁ†Å‰øÆÊîπÊàêÂäüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï')
      visible.value = false
      emit('success')
    }
    // ÈîôËØØÊ∂àÊÅØÁî±Êã¶Êà™Âô®Áªü‰∏ÄÂ§ÑÁêÜ
  } catch (error: any) {
    console.error('ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•:', error)
    console.error('ÈîôËØØËØ¶ÊÉÖ:', error.response?.data)
    // ÈîôËØØÊ∂àÊÅØÁî±Êã¶Êà™Âô®Áªü‰∏ÄÂ§ÑÁêÜÔºåËøôÈáå‰∏çÂÜçÈáçÂ§çÊòæÁ§∫
  } finally {
    loading.value = false
  }
}

defineExpose({
  open
})
</script>

<style scoped>
.dialog-footer {
  text-align: right;
}
</style>
