import request from '@/utils/request'

// ÁîüÊàêË∞∑Ê≠åÈ™åËØÅÁ†Å‰∫åÁª¥Á†Å
export const generateQrCode = (adminId: number) => {
  return request.get('/api/v1/admin/google-auth/qr-code', { params: { admin_id: adminId } })
}

// ÁªëÂÆöË∞∑Ê≠åÈ™åËØÅÁ†Å
export const bindGoogleAuth = (data: { admin_id: number; google_code: string; secret: string }) => {
  return request.post('/api/v1/admin/google-auth/bind', data)
}

// Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªëÂÆöË∞∑Ê≠åÈ™åËØÅÁ†Å
export const checkGoogleAuthBinding = (adminId: number) => {
  return request.get('/api/v1/admin/google-auth/check', { params: { admin_id: adminId } })
}
