<template>
  <div class="login-container">
    <!-- ËÉåÊôØË£ÖÈ•∞ -->
    <div class="background-decoration">
      <div class="floating-shapes">
        <div class="shape shape-1"></div>
        <div class="shape shape-2"></div>
        <div class="shape shape-3"></div>
        <div class="shape shape-4"></div>
      </div>
    </div>

    <!-- ‰∏ªË¶ÅÂÜÖÂÆπÂå∫Âüü -->
    <div class="login-content">
      <div class="login-card">
        <!-- Â§¥ÈÉ®LogoÂå∫Âüü -->
        <div class="login-header">
          <div class="logo-container">
            <img :src="logo" alt="Á≥ªÁªüLogo" class="logo" />
          </div>
          <h1 class="welcome-title">Ê¨¢ËøéÁôªÂΩï</h1>
          <p class="welcome-subtitle">Áôæ‰∫ø‰∏âÊñπÊîØ‰ªòÁÆ°ÁêÜÁ≥ªÁªü</p>
        </div>

        <!-- ÁôªÂΩïË°®Âçï -->
        <div class="login-form-container">
          <el-form 
            :model="loginForm" 
            class="login-form"
            label-position="top"
            size="large"
          >
            <div class="form-item">
              <el-form-item>
                <el-input
                  v-model="username"
                  placeholder="ËØ∑ËæìÂÖ•ÁôªÂΩïË¥¶Âè∑"
                  class="custom-input"
                  clearable
                >
                  <template #prefix>
                    <el-icon class="input-icon"><UserFilled /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>

            <div class="form-item">
              <el-form-item>
                <el-input
                  v-model="password"
                  type="password"
                  placeholder="ËØ∑ËæìÂÖ•ÁôªÂΩïÂØÜÁ†Å"
                  class="custom-input"
                  show-password
                  clearable
                >
                  <template #prefix>
                    <el-icon class="input-icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>

            <div v-if="showGoogleAuth" class="form-item google-auth-input">
              <el-alert
                title="ËØ∑ËæìÂÖ•Ë∞∑Ê≠åÈ™åËØÅÁ†Å"
                type="info"
                :closable="false"
                show-icon
                class="google-auth-alert"
              />
              <el-form-item>
                <el-input
                  v-model="googleAuthCode"
                  placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
                  class="custom-input"
                  clearable
                  maxlength="6"
                  @keyup.enter="loginData"
                >
                  <template #prefix>
                    <el-icon class="input-icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>


            <div class="form-submit">
              <el-button
                type="primary"
                size="large"
                class="login-button"
                @click="loginData"
                :disabled="!username || !password"
                :loading="loginLoading"
              >
                <span v-if="!loginLoading">ÁôªÂΩïË¥¶Âè∑</span>
                <span v-else>ÁôªÂΩï‰∏≠...</span>
              </el-button>
            </div>
          </el-form>
        </div>
      </div>
    </div>
    
    <!-- Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÁªëÂÆöÁªÑ‰ª∂ -->
    <GoogleAuthBind 
      ref="googleAuthBindRef" 
      @success="handleGoogleAuthSuccess"
    />

    <!-- ÂØÜÁ†Å‰øÆÊîπÁªÑ‰ª∂ -->
    <ChangePasswordDialog 
      ref="changePasswordRef" 
      @success="handlePasswordChangeSuccess"
    />
  </div>
</template>

<script lang="ts" setup>
import { ref, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { login } from "@/api/login.ts"
import { UserFilled, Lock } from '@element-plus/icons-vue'
import GoogleAuthBind from '@/components/GoogleAuthBind.vue'
import ChangePasswordDialog from '@/components/ChangePasswordDialog.vue' // New import
import logo from '@/assets/login-header.png'

const router = useRouter()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const username = ref('')
const password = ref('')
const googleAuthCode = ref('')
const showGoogleAuth = ref(false)
const loginLoading = ref(false)

// Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÁªëÂÆöÁõ∏ÂÖ≥
const googleAuthBindRef = ref()
const needBindGoogle = ref(false)
const tempUserInfo = ref<any>(null)

// ÂØÜÁ†Å‰øÆÊîπÁõ∏ÂÖ≥
const changePasswordRef = ref()
const needChangePassword = ref(false)
const changePasswordUserInfo = ref<any>(null)

// Ë°®ÂçïÊï∞ÊçÆ
const loginForm = ref({
  username: '',
  password: '',
  googleAuthCode: ''
})

const loginData = async () => {
  if (!username.value || !password.value) {
    ElMessage.error('ËØ∑ËæìÂÖ•Ë¥¶Âè∑ÂíåÂØÜÁ†Å')
    return
  }

  try {
    loginLoading.value = true
    
    const response = await login({
      username: username.value,
      password: password.value,
      google_code: showGoogleAuth.value ? googleAuthCode.value : '',
    })

    // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶Å‰øÆÊîπÂØÜÁ†Å
    if (response.data?.need_change_password) {
      needChangePassword.value = true
      changePasswordUserInfo.value = response.data
      changePasswordRef.value?.open(response.data.admin_id)
      return
    }

    // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅÁªëÂÆöË∞∑Ê≠åÈ™åËØÅÁ†Å
    if (response.data?.need_bind_google) {
      needBindGoogle.value = true
      tempUserInfo.value = response.data
      googleAuthBindRef.value?.open(response.data.admin_id, response.data.username)
      return
    }

    // ÁôªÂΩïÊàêÂäüÔºå‰øùÂ≠ò token
    // response ÊòØÂìçÂ∫îÊã¶Êà™Âô®ËøîÂõûÁöÑ resÔºåÊ†ºÂºè‰∏∫ { code: 200, status: true, message: 'ÁôªÂΩïÊàêÂäü', data: { Authorization: 'token' } }
    // ÊâÄ‰ª• response.data ÊòØ { Authorization: 'token' }
    const responseData = response as any
    console.log('üîç ÁôªÂΩïÂìçÂ∫îÊï∞ÊçÆ:', responseData)
    console.log('üîç responseData.data:', responseData.data)
    
    const token = responseData.data?.Authorization || responseData.data?.data?.Authorization || localStorage.getItem('token')
    console.log('üîç Ëé∑ÂèñÂà∞ÁöÑtoken:', token)
    console.log('üîç localStorage‰∏≠ÁöÑtoken:', localStorage.getItem('token'))
    
    // Ê£ÄÊü•ÁôªÂΩïÊòØÂê¶ÊàêÂäüÔºöstatus‰∏∫true„ÄÅcode‰∏∫200„ÄÅÊàñËÄÖÊúâtokenÈÉΩËÆ§‰∏∫ÊàêÂäü
    if (responseData.status === true || responseData.code === 200 || responseData.code === 0 || token) {
      // Á°Æ‰øù token Â∑≤‰øùÂ≠òÂà∞ localStorageÔºàÂìçÂ∫îÊã¶Êà™Âô®Â∑≤Áªè‰øùÂ≠ò‰∫ÜÔºåËøôÈáåÂÜçÊ¨°Á°ÆËÆ§Ôºâ
      if (token) {
        localStorage.setItem('token', token)
        console.log('‚úÖ ÁôªÂΩïÈ°µÈù¢ÂÜçÊ¨°‰øùÂ≠òtokenÔºåÈ™åËØÅ:', localStorage.getItem('token'))
        // Ëß¶ÂèëtokenÂ∑≤ËÆæÁΩÆÁöÑ‰∫ã‰ª∂ÔºåÈÄöÁü•App.vueÊõ¥Êñ∞Áä∂ÊÄÅ
        window.dispatchEvent(new CustomEvent('tokenSet', { detail: { token } }))
      } else {
        console.error('‚ùå token‰∏∫Á©∫ÔºåÊó†Ê≥ï‰øùÂ≠ò')
      }
      
      ElMessage.success('ÁôªÂΩïÊàêÂäü')
      // Á°Æ‰øù token Â∑≤‰øùÂ≠òÂêéÂÜçË∑≥ËΩ¨
      await nextTick()
      // ‰ΩøÁî® router.push Ë∑≥ËΩ¨
      router.push('/dashboard').catch(() => {
        // ÂøΩÁï•Ë∑ØÁî±ÈáçÂ§çÂØºËà™ÁöÑÈîôËØØ
      })
    } else {
      ElMessage.error(responseData.message || responseData.data?.message || 'ÁôªÂΩïÂ§±Ë¥•')
    }
  } catch (error: any) {
    // Ê£ÄÊü•ÊòØÂê¶ÊòØË¶ÅÊ±ÇËæìÂÖ•Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÁöÑÈîôËØØ
    if (error.message && error.message.includes('ËØ∑ËæìÂÖ•Ë∞∑Ê≠åÈ™åËØÅÁ†Å')) {
      // Áõ¥Êé•ÂºπÂá∫Ë∞∑Ê≠åÈ™åËØÅÁ†ÅËæìÂÖ•Ê°Ü
      showGoogleAuth.value = true
      // Ëá™Âä®ËÅöÁÑ¶Âà∞Ë∞∑Ê≠åÈ™åËØÅÁ†ÅËæìÂÖ•Ê°Ü
      setTimeout(() => {
        const googleAuthInput = document.querySelector('.google-auth-input .el-input__inner') as HTMLInputElement
        if (googleAuthInput) {
          googleAuthInput.focus()
        }
      }, 100)
    }
    // ÈîôËØØÊ∂àÊÅØÁî±Êã¶Êà™Âô®Áªü‰∏ÄÂ§ÑÁêÜÔºåËøôÈáå‰∏çÂÜçÈáçÂ§çÊòæÁ§∫
    console.error('ÁôªÂΩïÈîôËØØ:', error)
  } finally {
    loginLoading.value = false
  }
}

// Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÁªëÂÆöÊàêÂäüÂêéÁöÑÂ§ÑÁêÜ
const handleGoogleAuthSuccess = () => {
  needBindGoogle.value = false
  tempUserInfo.value = null
  ElMessage.success('Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÁªëÂÆöÊàêÂäüÔºåÈ°µÈù¢Â∞ÜËá™Âä®Âà∑Êñ∞ÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï')
  // Ê∏ÖÁ©∫Ë°®Âçï
  username.value = ''
  password.value = ''
  googleAuthCode.value = ''
}

// ÂØÜÁ†Å‰øÆÊîπÊàêÂäüÂêéÁöÑÂ§ÑÁêÜ
const handlePasswordChangeSuccess = () => {
  needChangePassword.value = false
  changePasswordUserInfo.value = null
  ElMessage.success('ÂØÜÁ†Å‰øÆÊîπÊàêÂäüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï')
  // Ê∏ÖÁ©∫Ë°®Âçï
  username.value = ''
  password.value = ''
  googleAuthCode.value = ''
}
</script>

