<template>
  <el-dialog
    v-model="visible"
    title="ÁªëÂÆöË∞∑Ê≠åÈ™åËØÅÁ†Å"
    width="500px"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :show-close="false"
    center
  >
    <div class="google-auth-bind">
      <!-- Ê≠•È™§1ÔºöÊòæÁ§∫‰∫åÁª¥Á†Å -->
      <div v-if="step === 1" class="step-content">
        <div class="step-header">
          <el-icon class="step-icon"><Key /></el-icon>
          <h3>Êâ´Êèè‰∫åÁª¥Á†Å</h3>
          <p>ËØ∑‰ΩøÁî®Ë∞∑Ê≠åÈ™åËØÅÂô®Êâ´Êèè‰∏ãÊñπ‰∫åÁª¥Á†Å</p>
        </div>
        
        <div class="qr-code-container">
          <div v-if="qrCodeLoading" class="qr-loading">
            <el-icon class="is-loading"><Loading /></el-icon>
            <p>ÁîüÊàê‰∫åÁª¥Á†Å‰∏≠...</p>
          </div>
          <div v-else-if="qrCodeData" class="qr-code">
            <img :src="qrCodeData.qr_code_url" alt="Ë∞∑Ê≠åÈ™åËØÅÁ†Å‰∫åÁª¥Á†Å" />
            <div class="qr-info">
              <p><strong>Ë¥¶Êà∑Ôºö</strong>{{ qrCodeData.account_name }}</p>
              <p><strong>ÂØÜÈí•Ôºö</strong>{{ qrCodeData.secret }}</p>
            </div>
          </div>
          <div v-else class="qr-error">
            <el-icon><Warning /></el-icon>
            <p>ÁîüÊàê‰∫åÁª¥Á†ÅÂ§±Ë¥•</p>
            <el-button @click="generateQrCode" type="primary">ÈáçËØï</el-button>
          </div>
        </div>
        
        <div class="step-actions">
          <el-button @click="nextStep" type="primary" :disabled="!qrCodeData">
            ‰∏ã‰∏ÄÊ≠•
          </el-button>
        </div>
      </div>
      
      <!-- Ê≠•È™§2ÔºöÈ™åËØÅÁªëÂÆö -->
      <div v-if="step === 2" class="step-content">
        <div class="step-header">
          <el-icon class="step-icon"><Lock /></el-icon>
          <h3>È™åËØÅÁªëÂÆö</h3>
          <p>ËØ∑ËæìÂÖ•Ë∞∑Ê≠åÈ™åËØÅÂô®‰∏≠ÁöÑ6‰ΩçÊï∞Â≠óÈ™åËØÅÁ†Å</p>
        </div>
        
        <el-form :model="bindForm" :rules="bindRules" ref="bindFormRef" label-width="0">
          <el-form-item prop="google_code">
            <el-input
              v-model="bindForm.google_code"
              placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
              maxlength="6"
              size="large"
              class="verification-input"
            >
              <template #prefix>
                <el-icon><Key /></el-icon>
              </template>
            </el-input>
          </el-form-item>
        </el-form>
        
        <div class="step-actions">
          <el-button @click="prevStep">‰∏ä‰∏ÄÊ≠•</el-button>
          <el-button 
            @click="bindGoogleAuthData" 
            type="primary" 
            :loading="bindLoading"
            :disabled="!bindForm.google_code"
          >
            ÂÆåÊàêÁªëÂÆö
          </el-button>
        </div>
      </div>
      
      <!-- ÁªëÂÆöÊàêÂäü -->
      <div v-if="step === 3" class="step-content">
        <div class="success-content">
          <el-icon class="success-icon"><CircleCheck /></el-icon>
          <h3>ÁªëÂÆöÊàêÂäü</h3>
          <p>Ë∞∑Ê≠åÈ™åËØÅÁ†ÅÂ∑≤ÊàêÂäüÁªëÂÆöÂà∞ÊÇ®ÁöÑË¥¶Êà∑</p>
          <el-button @click="handleSuccess" type="primary" size="large">
            ÂÆåÊàê
          </el-button>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Lock, Key, CircleCheck, Loading, Warning } from '@element-plus/icons-vue'
import { generateQrCode, bindGoogleAuth } from '@/api/google-auth'

const emit = defineEmits<{
  (event: 'success'): void
  (event: 'close'): void
}>()

const visible = ref(false)
const step = ref(1)
const qrCodeLoading = ref(false)
const bindLoading = ref(false)
const adminId = ref(0)
const qrCodeData = ref<any>(null)

const bindForm = reactive({
  google_code: ''
})

const bindRules = {
  google_code: [
    { required: true, message: 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å', trigger: 'blur' },
    { pattern: /^\d{6}$/, message: 'ËØ∑ËæìÂÖ•6‰ΩçÊï∞Â≠óÈ™åËØÅÁ†Å', trigger: 'blur' }
  ]
}

const bindFormRef = ref()

const open = (userId: number, username: string) => {
  visible.value = true
  step.value = 1
  adminId.value = userId
  generateQrCodeData()
}

const nextStep = () => {
  if (step.value < 3) {
    step.value++
  }
}

const prevStep = () => {
  if (step.value > 1) {
    step.value--
  }
}

const generateQrCodeData = async () => {
  try {
    qrCodeLoading.value = true
    const response = await generateQrCode(adminId.value)
    if (response.data) {
      qrCodeData.value = response.data
    } else {
      ElMessage.error('ÁîüÊàê‰∫åÁª¥Á†ÅÂ§±Ë¥•')
    }
  } catch (error) {
    console.error('ÁîüÊàê‰∫åÁª¥Á†ÅÂ§±Ë¥•:', error)
    ElMessage.error('ÁîüÊàê‰∫åÁª¥Á†ÅÂ§±Ë¥•')
  } finally {
    qrCodeLoading.value = false
  }
}

const bindGoogleAuthData = async () => {
  try {
    await bindFormRef.value?.validate()
    bindLoading.value = true
    
    const response = await bindGoogleAuth({
      admin_id: adminId.value,
      google_code: bindForm.google_code,
      secret: qrCodeData.value.secret
    })
    
    if (response.data) {
      ElMessage.success('ÁªëÂÆöÊàêÂäü')
      step.value = 3
    } else {
      ElMessage.error(response.data?.message || 'ÁªëÂÆöÂ§±Ë¥•')
    }
  } catch (error: any) {
    console.error('ÁªëÂÆöÂ§±Ë¥•:', error)
    ElMessage.error(error.message || 'ÁªëÂÆöÂ§±Ë¥•')
  } finally {
    bindLoading.value = false
  }
}

const handleSuccess = () => {
  visible.value = false
  emit('success')
  // ÁªëÂÆöÊàêÂäüÂêéÂà∑Êñ∞È°µÈù¢ÔºåËÆ©Áî®Êà∑ÈáçÊñ∞ÁôªÂΩï
  setTimeout(() => {
    window.location.reload()
  }, 1000)
}

// Êö¥Èú≤ÊñπÊ≥ï
defineExpose({
  open
})
</script>

